<template>
<div>
    <router-link  to="/login"><img class="close-account-page" src='@/assets/havr-icon/close-drawer-icon.svg'></router-link>
    <div class="form-stepper-create-account">
      <div class="jumbo">
  <h2 ><b>Create your Havr account </b></h2>
  <p >Please enter or click a button to validate a line</p>
      </div>
  <br><br>

        <div :class="{'hide-form' : currentStep < stepper.one}" class="section-form">
     <label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.one}">1</span>. Hello there, what's your firstname ?</label>
<form class="form-inline">
  <div class="form-group">
   
 
    <input v-model="user.first_name" :disabled="currentStep < stepper.one" type="text" class="form-control form-control-lg custom-size" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="eg. Jhon" required>
    <div class="invalid-feedback">
          Please enter your first name.
        </div>
  </div>
  <button v-if="currentStep === stepper.one"  @click="currentStep++"   class="btn btn-primary havr-color" :class="{activeButton : currentStep === stepper.one}">NEXT</button>
</form>

        </div>

 <div :class="{'hide-form' : currentStep < stepper.two}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.two}">2.</span> And your last name ?</label>
<form class="form-inline">
  <div class="form-group">
   
 
    <input v-model="user.last_name" :disabled="currentStep < stepper.two" type="text" class="form-control form-control-lg custom-size" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="eg. Doe" required>
     <div class="invalid-feedback">
          Please enter your last name.
        </div>
  </div>
  <button v-if="currentStep === stepper.two"  @click="currentStep++"  class="btn btn-primary havr-color" :class="{activeButton : currentStep === stepper.two}">NEXT</button>
</form>
 </div>
  <div :class="{'hide-form' : currentStep < stepper.three}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.three}" >3.</span> Whats your email ?</label>
<form class="form-inline">
  <div class="form-group">
   
 
    <input v-model="user.email" :disabled="currentStep < stepper.three" type="email" class="form-control form-control-lg custom-size" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="eg. jhon@doe.com" required>
     <div class="invalid-feedback">
          Please enter your email.
        </div>
  </div>
  <button v-if="currentStep === stepper.three"  @click="currentStep++"  class="btn btn-primary havr-color" :class="{activeButton : currentStep === stepper.three}">NEXT</button>
</form>
  </div>
   <div :class="{'hide-form' : currentStep < stepper.four}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.four}" >4.</span> Time to choose password</label>
<form class="form-inline">
  <div class="form-group">
   
 
    <input  v-model="user.password" :disabled="currentStep < stepper.four" type="password" class="form-control form-control-lg custom-size" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="8 characters min" required>
     <div class="invalid-feedback">
          Please enter a password.
        </div>
  </div>
  <button v-if="currentStep === stepper.four"  @click="currentStep++"  class="btn btn-primary havr-color" :class="{activeButton : currentStep === stepper.four}">NEXT</button>
</form>
   </div>
    <div :class="{'hide-form' : currentStep < stepper.five}" class="section-form">

<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.five}">5. </span> Please confirm password</label>
<form class="form-inline">
  <div class="form-group">
   
 
    <input v-model="checkPasswordData" :disabled="currentStep < stepper.five" type="password" class="form-control form-control-lg custom-size" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="same as before" required>
     <div class="invalid-feedback">
          Please confirm your password.
        </div>
  </div>
  <button v-if="currentStep === stepper.five"  @click="currentStep++" class="btn btn-primary havr-color" :class="{activeButton : currentStep === stepper.five}">NEXT</button>
</form>

    </div>
     <div :class="{'hide-form' : currentStep < stepper.six}" class="section-form">
<label  class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.six}">6.</span> Finally, choose a username</label>
<form>
  <div class="form-group">
   
 
    <input v-model="user.username" :disabled="currentStep < stepper.six" type="text" class="form-control form-control-lg custom-size" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="eg. jhon02" required>
     <div class="invalid-feedback">
          Please enter a username.
        </div>
  </div>
  <br>
  <button :disabled="canCreateAccount" v-if="currentStep === stepper.six"  @click="createAccount()"  class="btn btn-primary havr-color" :class="{activeButton : currentStep === stepper.six}">CREATE ACCOUNT</button>
</form>
     </div>

<img class="side-brand" src="@/assets/img/havr-side-brand.png">
    </div>
</div>
</template>



<script>
import repository from "@/repository/users";
import notifiy from "vue-notification";
export default {
  data() {
    return {
      checkPasswordData: "",
      currentStep: 1,
      user: {
        email: "",
        username: "",
        first_name: "",
        last_name: "",
        password: ""
      },
      stepper: {
        one: 1,
        two: 2,
        three: 3,
        four: 4,
        five: 5,
        six: 6
      }
    };
  },
  computed: {
    canCreateAccount() {
        for(const i in this.user) {
            if(this.user[i].trim() === '') return true
        }
    }
  },
  methods: {
    async createAccount() {
      if (this.checkPasswordData !== this.user.password)  return this.$notify({
        message: "Error : Password doesnt match",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
      const user = { user: this.user };
      const resp = await repository.createUserAccount(user);
      this.user = {
        email: "",
        username: "",
        first_name: "",
        last_name: "",
        password: ""
      };
     
    }
  }
};
</script>


